html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Roboto Light";
  src: url("../fonts/roboto-light-webfont.woff2") format("woff2"), url("../fonts/roboto-light-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Regular";
  src: url("../fonts/roboto-regular-webfont.woff2") format("woff2"), url("../fonts/roboto-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/roboto-medium-webfont.woff2") format("woff2"), url("../fonts/roboto-medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Bold";
  src: url("../fonts/roboto-bold-webfont.woff2") format("woff2"), url("../fonts/roboto-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.icon-back {
  width: 1em;
  height: 1em;
  fill: #231F20;
}

.icon-check {
  width: 1em;
  height: 1em;
  fill: #ea632f;
}

.icon-messenger {
  width: 1em;
  height: 1em;
  fill: #3692fb;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 0.79em;
  height: 1em;
  fill: initial;
}

.icon-refresh {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-telegram {
  width: 1em;
  height: 1em;
  fill: #5ebee1;
}

.icon-viber {
  width: 1em;
  height: 1em;
  fill: #7d3daf;
}

.icon-whatsapp {
  width: 1em;
  height: 1em;
  fill: #189d0e;
}

body {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.5;
  color: #444444;
  background: #fff;
}

.logo {
  display: block;
  width: 100%;
}

.logo img {
  max-width: 100%;
  display: block;
}

.title {
  font-size: 36px;
  line-height: 1.33;
  font-family: 'Roboto Medium', Arial, Helvetica, sans-serif;
  font-weight: normal;
  padding-bottom: 8px;
  position: relative;
  display: inline-block;
  margin-bottom: 36px;
}

.title:before {
  content: "";
  display: block;
  width: 400px;
  max-width: 50%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: #ea632f;
  height: 4px;
}

.section {
  padding: 80px 0;
  text-align: center;
  overflow: hidden;
}

.orange {
  color: #ea632f;
}

.scroll-up {
  border: 2px solid #ea632f;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scroll-up .icon {
  fill: #ea632f;
  font-size: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -2px;
}

.l {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

.in {
  margin-bottom: 188px;
  background: #fff;
  box-shadow: 2px 2px 16px rgba(0, 0, 0, 0.15);
}

.button {
  appearance: none;
  z-index: 1;
  display: inline-block;
  position: relative;
  overflow: hidden;
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  padding: 16px 36px;
  border-radius: 100px;
  text-decoration: none;
  border: 1px solid #ea632f;
  background: transparent;
  color: #fff;
  font-size: 18px;
  white-space: nowrap;
  transition: color .4s;
}

.button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(0) skew(0);
  transition: transform .4s;
  background: #ea632f;
  z-index: -2;
}

.button:hover {
  color: #ea632f;
}

.button:hover:before {
  transform: translateX(-120%) skew(-45deg);
}

.button_special {
  padding: 6px 12px;
  font-size: 14px;
  border-color: #fff;
}

.button_special:before {
  background: #fff;
  transform: translateX(-110%) skew(-45deg);
}

.button_special:hover {
  color: #ea632f;
}

.button_special:hover:before {
  transform: translateX(0) skew(0);
}

.button .icon {
  fill: currentColor;
  display: inline-block;
  vertical-align: middle;
  margin: -1px 4px 0 0;
}

.header {
  border-bottom: 10px solid #ea632f;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}

.header__in {
  display: flex;
  height: 60px;
}

.header__logo {
  width: 190px;
  align-self: flex-end;
  position: relative;
  top: 14px;
}

.header__nav {
  flex-grow: 1;
  text-align: right;
  align-self: center;
}

.nav a {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  text-decoration: none;
  color: #fff;
  padding: 8px 12px;
  margin-left: 8px;
  position: relative;
  transition: color .25s ease-in-out;
}

.nav a:hover {
  color: #ea632f;
}

.nav a:hover .icon {
  fill: #ea632f;
}

.nav .icon {
  fill: #fff;
  font-size: 16px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -1px;
  transition: fill .25s ease-in-out;
}

.footer {
  background: #444444;
  color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 188px;
  z-index: -1;
}

.footer:before {
  content: "";
  display: block;
  position: absolute;
  bottom: 28px;
  left: 0;
  width: 100%;
  height: 10px;
  background: #ea632f;
}

.footer__in {
  display: flex;
  justify-content: space-between;
}

.footer__logo {
  width: 190px;
  position: relative;
  align-self: flex-end;
  bottom: 24px;
}

.footer__form {
  width: calc(100% - 190px);
  max-width: 600px;
  padding: 54px 0 54px 96px;
}

.hero {
  min-height: 100vh;
  background: #444444;
  color: #fff;
  position: relative;
  overflow: hidden;
}

.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.hero__bg:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.hero__bg:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 4px 4px;
  background-image: radial-gradient(circle, #000 1px, transparent 1px);
  background-repeat: repeat;
  z-index: 4;
}

.hero__bg img, .hero__bg video {
  width: 150%;
  height: 150%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__bg img {
  z-index: 1;
}

.hero__bg video {
  z-index: 2;
}

.hero__in {
  padding-top: 60px;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  text-align: center;
}

.hero__title {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 48px;
  line-height: 1.33;
  margin-bottom: 48px;
}

.hero__text {
  font-size: 22px;
  line-height: 1.5;
  max-width: 720px;
  margin: 0 auto 48px;
}

.widgets {
  display: flex;
}

.widget {
  width: 25%;
  height: 200px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  display: flex;
  align-items: center;
  color: #fff;
  position: relative;
}

.widget:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 0;
  transition: opacity .25s ease-in-out;
}

.widget:hover:before {
  opacity: 0;
}

.widget:hover .widget__in {
  color: #fff;
}

.widget__in {
  padding: 16px 0;
  position: relative;
  z-index: 1;
  color: #ececec;
  transition: color .25s ease-in-out;
}

.widget__numb {
  font-family: 'Roboto Medium', Arial, Helvetica, sans-serif;
  font-size: 64px;
  padding: 0 32px;
}

.widget__desc {
  padding: 6px 16px 6px 32px;
  position: relative;
  background: #ea632f;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
}

.mission {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.mission__in {
  display: flex;
  align-items: center;
}

.mission__info {
  width: 50%;
  color: #fff;
  padding: 0 64px 0 0;
  margin-top: -10px;
}

.mission__text {
  font-size: 18px;
}

.mission__media {
  width: 50%;
  position: relative;
}

.mission__media img {
  max-width: 100%;
  display: block;
}

.btn-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid #ea632f;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  background: none;
  font-size: 28px;
  opacity: .8;
  transition: transform .25s ease-in-out, opacity .25s ease-in-out;
}

.btn-play:hover {
  transform: translate(-50%, -50%) scale(1.2);
  opacity: 1;
}

.btn-play .icon {
  fill: #ea632f;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  margin-left: 3px;
}

.values {
  background: #ececec;
}

.values__boxes {
  display: flex;
  max-width: 960px;
  margin: 0 auto;
  padding-top: 36px;
}

.value {
  padding: 0 32px;
  width: 25%;
}

.value:hover .value__text:before {
  transform: translateX(-50%) scale(1);
}

.value:hover .value__img {
  transform: translateY(-4px);
}

.value__img {
  width: 64px;
  height: 64px;
  margin-bottom: 24px;
  transition: transform .25s ease-in-out;
}

.value__text {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  position: relative;
}

.value__text:before {
  content: "";
  display: block;
  width: 75px;
  max-width: 75%;
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%) scale(0);
  background: #ea632f;
  height: 2px;
  transition: transform .25s ease-in-out;
}

.advantages__boxes {
  display: flex;
  justify-content: space-between;
  padding-top: 36px;
}

.box {
  width: 18%;
  background: #fff;
  box-shadow: -2px 4px 24px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  padding: 32px 24px;
  text-align: center;
  transition: transform .25s ease-in-out, box-shadow .25s ease-in-out;
}

.box:not(:last-child) {
  margin-right: 16px;
}

.box:hover {
  transform: translate3d(0, -6px, 0);
  box-shadow: -2px 10px 24px 4px rgba(0, 0, 0, 0.15);
}

.box__img {
  margin-bottom: 24px;
}

.box__text {
  font-size: 14px;
}

.reasons {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  color: #fff;
}

.reasons__list {
  padding-top: 36px;
  margin-bottom: 48px;
}

.reasons__list ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.reasons__list li {
  font-size: 18px;
  width: 40%;
  margin-bottom: 32px;
  padding: 0 32px;
}

.mentors__list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 36px;
  width: calc(100% + 24px);
}

.mentor {
  width: calc(20% - 24px);
  margin: 0 24px 24px 0;
  padding: 24px;
  height: 240px;
  position: relative;
  display: flex;
  color: #fff;
}

.mentor:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #c0c0c0;
  z-index: -1;
}

.mentor:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, transparent 0%, #ea632f 100%);
}

.mentor__in {
  position: relative;
  z-index: 1;
  width: 100%;
  align-self: flex-end;
  text-align: center;
}

.mentor__pic {
  display: block;
  height: 90%;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}

.mentor__name {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 1px;
  margin-bottom: 12px;
}

.faq {
  background: #ececec;
}

.faq__list {
  text-align: left;
}

.f-item {
  margin-bottom: 36px;
}

.f-item.is-active .f-item__title {
  color: #444444;
}

.f-item__title {
  padding: 12px 0;
  font-family: 'Roboto Medium', Arial, Helvetica, sans-serif;
  font-weight: normal;
  cursor: pointer;
  transition: color .25s ease-in-out;
  color: rgba(0, 0, 0, 0.5);
}

.f-item__title:hover {
  color: #444444;
}

.f-item__list ul {
  padding-left: 16px;
}

.f-item__list li {
  position: relative;
  padding-left: 32px;
  margin-bottom: 8px;
}

.f-item__list li:before {
  content: "";
  width: 24px;
  height: 24px;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  position: absolute;
  top: 2px;
  left: 0;
}

.f-item__list a {
  color: #ea632f;
  text-decoration: none;
  transition: opacity .25s ease-in-out;
}

.f-item__list a:hover {
  opacity: .75;
}

.form {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.form__title {
  color: #fff;
}

.form__title:before {
  display: none;
}

.form-box {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  padding: 32px 48px;
}

.form-box:hover {
  transform: none;
}

.form-box:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  max-width: 30%;
  background: #ea632f;
  height: 4px;
}

.form-box__text {
  font-family: 'Roboto Medium', Arial, Helvetica, sans-serif;
  font-size: 24px;
  margin-bottom: 36px;
}

.input {
  margin-bottom: 36px;
  text-align: left;
}

.input.is-valid input {
  border-color: #ea632f;
}

.input.is-invalid input {
  border-color: #ff0000;
  box-shadow: 1px 1px 12px 1px rgba(255, 0, 0, 0.25);
}

.input label {
  display: block;
  margin-bottom: 8px;
}

.input input {
  width: 100%;
  height: 44px;
  border: 1px solid #c0c0c0;
  border-radius: 2px;
  font-size: 16px;
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  padding: 0 12px;
  transition: border-color .25s ease-in-out;
}

.input input:focus {
  border-color: #ea632f;
}

.input_special {
  display: flex;
  align-items: center;
  position: relative;
}

.input_special input {
  border-radius: 100px;
  background: transparent;
  border-color: #ea632f;
  color: #fff;
  padding: 0 16px;
}

.input_special input::placeholder {
  color: #fff;
}

.input_special .button {
  padding: 10px 20px 10px 16px;
  position: absolute;
  right: 0;
  top: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  font-size: 16px;
}

.contacts__person {
  padding: 48px 64px;
  background: #ececec;
  border-radius: 4px;
  max-width: 960px;
  margin: 0 auto 48px;
}

.contacts__name {
  font-family: 'Roboto Medium', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 24px;
  color: #000;
}

.contacts__position {
  font-family: 'Roboto Regular', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  margin-bottom: 24px;
}

.contacts__pic {
  max-width: 600px;
  margin: 0 auto;
  position: relative;
}

.contacts__pic:before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: #ea632f;
}

.contacts__pic img {
  max-width: 100%;
  display: block;
}

.contacts__phone {
  font-size: 24px;
  margin-bottom: 48px;
}

.contacts__phone a {
  text-decoration: none;
  color: currentColor;
  transition: color .25s ease-in-out;
}

.contacts__phone a:hover {
  color: #ea632f;
}

.socials {
  display: inline-flex;
}

.socials a {
  width: 84px;
  height: 84px;
  text-decoration: none;
}

.socials a:not(:last-child) {
  margin-right: 48px;
}

.socials .icon {
  width: inherit;
  height: inherit;
  transition: fill .25s ease-in-out;
}

.socials .icon:not(:hover) {
  fill: #ececec;
}
