.footer {
    background: $black;
    color: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 188px;
    z-index: -1;
    &:before {
        content: "";
        display: block;
        position: absolute;
        bottom: 28px;
        left: 0;
        width: 100%;
        height: 10px;
        background: $orange; }
    &__in {
        display: flex;
        justify-content: space-between; }
    &__logo {
        width: 190px;
        position: relative;
        align-self: flex-end;
        bottom: 24px; }
    &__form {
        width: calc(100% - 190px);
        max-width: 600px;
        padding: 54px 0 54px 96px; } }
